.article {
  position: relative;
  padding: 40px 0;
  margin-top: 132px;
}
.article h2 {
  font-size: 30px;
  margin-bottom: 20px;
  font-weight: 400;
}
@media only screen and (max-width: 900px) {
  .article h2 {
    font-size: 25px;
  }
}
@media only screen and (max-width: 900px) {
  .article ul, .article ol {
    padding-left: 20px;
  }
}
.article li {
  margin: 6px 0;
}
@media only screen and (max-width: 1200px) {
  .article {
    margin-top: 98px;
  }
}
@media only screen and (max-width: 900px) {
  .article {
    margin-top: 70px;
    padding-bottom: 0;
  }
}
.article__info {
  display: flex;
  gap: 8px;
  margin: 20px 0;
}
.article__info > div {
  background-color: #dd9933;
  color: #fff;
  padding: 6px;
  border-radius: 3px;
}
@media only screen and (max-width: 600px) {
  .article__info {
    flex-direction: column;
    text-align: center;
  }
}
.article__banner {
  padding: 20px;
  border-radius: 20px;
  background-color: rgb(217, 83, 79);
  border: 2px solid #000;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-weight: 600;
}
.article__banner > a {
  font-weight: 600;
  color: #dd9933;
}
@media only screen and (max-width: 900px) {
  .article__banner {
    flex-direction: column;
    gap: 6px;
  }
}